import axios from "axios";
// axios service
import { ElMessage } from "element-plus";
const service = axios.create({
  headers: {
    "Cache-Control": "no-cache",
  },
});
// Interceptors
service.defaults.headers.post["Content-Type"] =
  "application/json;charset=UTF-8";
export const BASE_NAME = "/lzzApis_V2";
export const BASE_FILE_NAME = "/fileApi";

export const LOCAL_STORAGE = "lzz_V2_token_local_storage";

export const ResponseCode = {
  success: 200,
};
service.interceptors.request.use(
  function (config) {
    config.headers["CUSTOMIZE-CLIENT-TYPE"] = "web";
    const token = sessionStorage.getItem(LOCAL_STORAGE);
    if (token) {
      // config.params = { token: token } 
      config.headers.Authorization = `Bearer ${token}`;
    }
    // 
    return config;
  },
  function (error) {
    // 
    return Promise.reject(error);
  }
);

// 
service.interceptors.response.use(
  function (response) {
    // 
    return response;
  },
  function (error) {
    if (error && error.response) {
      // 1.
      // 2.
      switch (error.response.status) {
        case 400:
          error.message = "";
          break;
        case 401:
          error.message = "token";
          sessionStorage.removeItem(LOCAL_STORAGE);
          router.replace({
            path: "/login",
          });
          break;
        case 403:
          error.message = "";
          break;
        case 404:
          error.message = ",";
          break;
        case 405:
          error.message = "";
          break;
        case 408:
          error.message = "";
          break;
        case 500:
          error.message = "!";
          break;
        case 501:
          error.message = "";
          break;
        case 502:
          error.message = "";
          break;
        case 503:
          error.message = "";
          break;
        case 504:
          error.message = "";
          break;
        case 505:
          error.message = "http";
          break;
        default:
          error.message = `${error.response.status}`;
      }
    } else {
      // 
      if (JSON.stringify(error).includes("timeout")) {
        error.message = "";
      }
      error.message = "";
    }
    ElMessage.error(error.message);
    // 
    return Promise.reject(error);
  }
);

export default service;
